import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'	
export default {
	data () {
		return {
			page: 1,
			limit: 10,
			pointList: [], // 积分记录
			loadStatus: 'more'
		}
	},
	components: { uniLoadMore },
	onLoad () {
		this.userPointLog()
	},
	computed: {
		nowDate () {
			return this.$common.timeToDate(Math.round(new Date().getTime()/1000))
		}
	},
	methods: {
		userPointLog () {
			let _this = this
			let data = {
				page: _this.page,
				limit: _this.limit
			}
			
			_this.loadStatus = 'loading'
			
			_this.$api.pointLog(data, function (res) {
				if (res.status) {
					_this.pointList = [..._this.pointList, ...res.data]
					// 判断数据是否加载完毕
					if (res.count > _this.pointList.length) {
						_this.page ++
						_this.loadStatus = 'more'
					} else {
						_this.loadStatus = 'noMore'
					}
				} else {
					// 接口請求出錯
					_this.$common.errorToShow(res.msg)
					_this.loadStatus = 'more'
				}
			})
		}
	},
	// 页面滚动到底部触发事件
	onReachBottom () {
		let _this = this
		if (_this.loadStatus === 'more') {
			_this.userPointLog()
		}
	}
}	
